<script>
import NfeDialog from "../components/NfeDialog";
import CalendarSelect from "../components/CalendarSelect";

export default {
  components: {
    modal_nfe_dialog_chave44: NfeDialog,
    modal_nfe_dialog_chavenf: NfeDialog,
    modal_nfe_dialog_cnpj_emissor: NfeDialog,
    modal_nfe_dialog_cnpj_destinatario: NfeDialog,
    calendar_select: CalendarSelect,
  },
  data: (vm) => ({
    publicPath: process.env.BASE_URL,
    dialog_chave44: false,
    dialog_chavenf: false,
    dialog_nfe_cnpj_emissor: false,
    dialog_nfe_cnpj_destinario: false,
    dialog_nfe_empresa: false,
    props_chave44: "",
    props_chavenf: "",
    props_cnpj_emissor: "",
    props_cnpj_destinatario: "",

    showTable: false,
    showButton: true,

    errors: [],

    opt_empresa: [],
    opt_value_empresa: [],

    dataInicio: "",
    dataFim: "",

    titulo_variante: "",
    desc_variante: "",

    job_return: 
      {
        "status":"",
        "jobname":"",
        "errorMsg":""
      },
    
    jobFinishRun: "false",
    jobSchedulerFinished: "",
    v_excelParam: false,
    v_excelLoading: true,

    v_fillExcelFields: false,

      csv_rotate :"360",
      csv_size: 100,
      csv_width: "15",
      csv_value: 0,


        headers: [
          { text: 'LINHA',          value: 'linha', align: 'start' },
          { text: 'CHAVE44',        value: 'chave44', align: 'right' },
          { text: 'NFNUM',          value: 'chavenf', align: 'right' },
          { text: 'CNPJ_EMISSOR',   value: 'cnpj_emissor_de', align: 'right' },
          { text: 'CNPJ_EMISSOR_ATE',   value: 'cnpj_emissor_ate', align: 'right' },
          { text: 'CNPJ_RECEBEDOR', value: 'cnpj_recebedor_de', align: 'right' },
          { text: 'CNPJ_RECEBEDOR_ATE', value: 'cnpj_recebedor_ate', align: 'right' },
          { text: 'DATA',           value: 'data_de',  align: 'right', sortable: false },
          { text: 'DATA_ATE',           value: 'data_ate',  align: 'right', sortable: false },
          { text: 'EMPRESA',          value: 'empresa', align: 'right' },
        ],
        values: [],
        errorValues: [],
        
        v_erroDescDump: ""    

  }),

  computed: {
    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.keys.filter((key) => key !== `Name`);
    },

    //setData(mydataInicio, mydataFim) {
      //this.dataInicio = mydataInicio;
      //this.dataFim = mydataFim;
   //  },
  },

  watch: {},

  beforeMount() {
    this.fetchEmpresas();
  },

  methods: {



      checkForm: function (e) {
            this.errors = [];

            if (this.titulo_variante == "") {
              this.errors.push('O campo "Titulo Consulta" é obrigatório.');
            }
            if (this.desc_variante == "") {
              this.errors.push('O campo "Descrição Consulta" é obrigatório.');
            } 

            if ( this.v_excelParam == false )
            {
              if (this.$store.getters.nfe_getFiltrosDataInicioConsulta == "") {
                this.errors.push('O campo "Data Inicial (De)" é obrigatório.');
              } 

              if (this.opt_value_empresa.length == 0) {
              this.errors.push('O campo "Empresas" é obrigatório. Você precisa selecionar a empresa que irá realizar a consulta solicitada.');
            } 

            }
    

            if (this.errors.length == 0) {
              return true;
            }

           // e.preventDefault();
          },


    /*AXIOS METHODS*/
    fetchEmpresas: function () {
      const baseURI = this.$host + "companies?$format=json";
      this.$http
        .get(baseURI, 
         {
            //auth: {
            //  username: "tjuliocst",
            //  password: "elitebox", // Bad password
            //},
         }
        )
        .then((result) => {
          var empresas = [];
          result.data.d.results.forEach((empresa) => {
            empresas.push(empresa.bukrs);
            this.opt_value_empresa.push(empresa.bukrs);
          });

          this.$store.dispatch("nfe_setEmpresas", empresas);
        })
        .catch((error) => {
          //console.log("errro carregar empresas:" + error);
        });
    },

    analisarSolicitacao() {

      var goAhead = this.checkForm()

      if(goAhead == true)
      {

      const baseURI = this.$host + "searchDocuments";

      this.showButton = false

      var chaves=[]
      var nnfes=[]
      var emitcnpj=[]
      var destcnpj=[]
      var empresas=[]
      var local_empresas=[]

      var data=""

      var root_json = ""
      
      if (this.opt_value_empresa.length > 0) {
                        
                                      this.opt_value_empresa.forEach((empresa) => {
                                        empresas.push({"bukrs":empresa})
                                      })
                              } 

      if (this.v_excelParam != true)
      {

      if (this.$store.getters.nfe_getFiltrosChave44.length > 0) {
                  
                                this.$store.getters.nfe_getFiltrosChave44.forEach((chave44) => {
                                  chaves.push( {"id": chave44.fieldname} )
                                })
                        } 

      if (this.$store.getters.nfe_getFiltrosChaveNf.length > 0) {
                        
                                      this.$store.getters.nfe_getFiltrosChaveNf.forEach((ide_nnf) => {
                                        nnfes.push( {"ide_nnf": ide_nnf.fieldname} )
                                      })
                              } 
      
       if (this.$store.getters.nfe_getFiltrosNfeEmissor.length > 0) {
                        
                                      this.$store.getters.nfe_getFiltrosNfeEmissor.forEach((emit_cnpj) => {
                                        emitcnpj.push( {"de":emit_cnpj.de, "ate":emit_cnpj.ate} )
                                      })
                              } 

       if (this.$store.getters.nfe_getFiltrosNfeDestinatario.length > 0) {
                        
                                      this.$store.getters.nfe_getFiltrosNfeDestinatario.forEach((dest_cnpj) => {
                                        destcnpj.push({"de":dest_cnpj.de, "ate":dest_cnpj.ate})
                                      })
                              } 


      }
     
     if (this.v_excelParam == true)
      {

      var filtro = [] 
      var count = 0
      //{chave44:`${line[0]}`, chavenf: `${line[1]}`, cnpj_emissor_de: `${line[2]}`, cnpj_emissor_ate: `${line[3]}`, cnpj_recebedor_de: `${line[4]}`, cnpj_recebedor_ate: `${line[5]}`, data_de:`${line[6]}`, data_ate:`${line[7]}`}
      this.values.forEach((line) => {

                                  if (line.chave44.length > 0)
                                  {
                                  chaves.push(  {"id": line.chave44}  )
                                  
                                  }

                                  if (line.chavenf.length > 0)
                                  {
                                  nnfes.push(  {"ide_nnf": line.chavenf}  )
                                  }
                                  
                                  if (line.cnpj_emissor_de.length > 0)
                                  {
                                  emitcnpj.push(  {"de":line.cnpj_emissor_de, "ate":line.cnpj_emissor_ate}  )
                                  }
                                  
                                  if (line.cnpj_recebedor_de.length > 0)
                                  {
                                  destcnpj.push(  {"de":line.cnpj_recebedor_de, "ate":line.cnpj_recebedor_ate}  )
                                  }

                                  //if (line.data_de.length > 0)
                                  //{
                                  data = { "inicial" : line.data_de, "final" : line.data_ate } 
                                  //}

                                  
                                  local_empresas.push( {"bukrs" : line.empresa.replace('\r', '') } )

                                  if (line.empresa.trim() == "")
                                  {
                                      local_empresas = empresas                                    
                                  }

     
     filtro.push( {              "chave": chaves,
                                  "nnfes": nnfes,
                                  "emit_cnpj": emitcnpj,
                                  "dest_cnpj": destcnpj,
                                  "empresas": local_empresas,
                                  "data": data
                              })
      count++
      //console.log(filtro)
      
      chaves = []
      nnfes = []
      emitcnpj = []
      destcnpj = []
      data =  ""
      local_empresas = []

      })

      root_json = {
                              "title": this.titulo_variante,
                              "desc": this.desc_variante,
                              "docType": "nfein", 
                              "filtros" : filtro
                            }

          //console.log("excel-payload")
          //console.log(root_json)

      }
      else
      {
      root_json = {
                        "title": this.titulo_variante,
                        "desc": this.desc_variante,
                        "docType": "nfein", // nfein e cte
                        "filtros" : [{
                            "chave":chaves,
                            "nnfes":nnfes, //nctes
                            "emit_cnpj":emitcnpj, // rem_cnpj, recebe_cnpj, toma_cnpj
                            "dest_cnpj":destcnpj,
                            "empresas":empresas,
                            "data": {
                                      "inicial": this.$store.getters.nfe_getFiltrosDataInicioConsulta, 
                                      "final":this.$store.getters.nfe_getFiltrosDataFimConsulta
                                    }
                        }]
                      }
      }

      ////console.log(root_json)

       root_json = JSON.stringify(root_json)
       //root_json = JSON.stringify(root_json)

       root_json = {"json": root_json }

       //console.log ("json string>")
       //console.log(root_json)
     
      this.$http
        .post(baseURI, root_json, 
        {
         //withCredentials: true,

           //auth: {
              //username: "tjuliocst",
              //password: "elitebox", // Bad password
            //},
            headers: {
                        'x-csrf-token': this.$store.getters.nfe_getCsrfToken,
                        'Content-Type': 'application/json'
                      }

        }
        )
        .then((result) => {
            //console.log("envaio gatewy>")
            //console.log(result)
            this.job_return = JSON.parse(result.data.d.json)
            this.showTable = true
            this.buscaRetornoSchedulerSolicitacao( this.job_return.status, this.job_return.jobname )          
          
        })
        .catch((error) => {
          //console.log("consultar status job scheduler analise:");
           //console.log(error);
           this.showButton = true

          
        });
       
      }
    },

    buscaRetornoSchedulerSolicitacao : function(status, id)
    {
                
          if ( status == 'S')
            {   var controlInterval
                const retryCount = 5
                const retrySeconds = 5000
                var totalRetry = 43200
                var countRetry = 0
                var check

                setTimeout(() => {
                }, retrySeconds);
                
                controlInterval = setInterval(() => {
                  //console.log("aguardando 5 segundos...")
                  if (totalRetry <= countRetry)
                  {
                        clearInterval(controlInterval)
                  }                    
                  else
                  {
                    this.api_buscaStatusJobScheduler(id)

                    if (this.jobFinishRun == "true")
                    {
                      this.jobSchedulerFinished = true
                      clearInterval(controlInterval)
                    }

                  }  
                    countRetry++

                }, 10000);



              }

    },

    api_buscaStatusJobScheduler(job_id)
    {       //#! checar a questao de fazer mesma busca mais de uma vez
            const baseURI = this.$host + `/jobs('${job_id}')?$format=json`;

            this.$http
              .get(baseURI, {
                  //auth: {
                    //username: "tjuliocst",
                    //password: "elitebox", // Bad password
                  //},
              }
              )
              .then((result) => {


                //console.log("resultJobResponse:")
                //console.log(result.data.d.status)

                if (result.data.d.status == "B")
                {
                  this.jobFinishRun = "true"
                }
                else if (result.data.d.status == "E")
                  {
                    this.jobFinishRun = "true"
                    this.v_erroDescDump = result.data.d.error_desc
                  }


              })
              .catch((error) => {
                //console.log("errro carregar buscaStatusJobScheduler:" + error);
                 this.jobFinishRun = "False"
              });
    },

    api_buscaDetalhesJobScheduler(job_id)
    {
            const baseURI = this.$host + `/jobHeaders(jobname='${job_id}',company='*')?$expand=navJobHeaderToJobHeaders,navJobHeadersToJobFilters&$format=json`;

            this.$http
              .get(baseURI, {
                //  auth: {
                //    username: "tjuliocst",
                //    password: "elitebox", // Bad password
                //  },
              })
              .then((result) => {
                //console.log("resultJobResponse:")
                //console.log(result)
                return true
              })
              .catch((error) => {
                //console.log("errro carregar empresas:" + error);
                return false
              });
    },
    
