<template>
  
    <v-row justify="center">
    <!--chave44 -->
    <v-dialog v-model="props_showLogDetails" v-if="props_messageType == 'E'" persistent max-width="80%">

            <v-card>
                <v-card-title>
                 <slot></slot>
                <v-spacer></v-spacer>
                <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                ></v-text-field>
                </v-card-title>
                <v-data-table
                :headers="headers_error"
                :items="values"
                :search="search"
                :loading="v_tableloading"
                ></v-data-table>
                 <v-pagination 
                  v-model="page"
                  :length="pageCount"
                  :total-visible="7"

                ></v-pagination>
            </v-card>

           

     <v-btn @click="$emit('closemodal')"> FECHAR </v-btn>
     </v-dialog>

    <v-dialog v-model="props_showLogDetails" v-if="props_messageType == 'S'" persistent max-width="80%">
       <v-card>
                <v-card-title>
                 <slot></slot>
                <v-spacer></v-spacer>
                <!--v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                ></v-text-field-->
                </v-card-title>
                <v-data-table
                :headers="headers_items"
                :items="values"
                :page.sync="page"
                :items-per-page="itemsPerPage"
                hide-default-footer
                
                :search="search"
                :loading="v_tableloading"
                ></v-data-table>
                <v-pagination 
                  v-model="page"
                  :length="pageCount"
                  :total-visible="7"
                  
                ></v-pagination>
            </v-card>
          <v-btn @click="$emit('closemodal')"> FECHAR </v-btn>
     </v-dialog>

    </v-row>
    
</template>

<script>
export default {
  name: 'DialogLogViewer',

  props: { 
    props_showLogDetails: Boolean,
    props_messageType: String,
    props_servico: String,
    props_jobname: String,
    props_company: String,
    props_code: String
  },

  data: () => ({ 

      v_tableloading: true,
      v_checkDouble: false,

        page: 1,
        pageCount: 0,
       
        itemsPerPage: 10,
        nextPage: 0,
        backPage: 0,
        total_visible: "10",


      search: '',
        headers_error: [
          {
            text: 'Documento',
            align: 'start',
            sortable: false,
            value: 'doc',
          },
          { text: 'mensagem', value: 'msg' },
          { text: 'codigo', value: 'codigo' },
        
        ],

        headers_items: [
          {
            text: 'Empresa',
            align: 'start',
            sortable: false,
            value: 'company',
          },
          { text: 'chave44', value: 'chave44' },
          { text: 'nnfe', value: 'nnfe' },
          { text: 'cnpj_emitente', value: 'cnpj_emit' },
          { text: 'cnpj_destinatario', value: 'cnpj_dest' },
          { text: 'data_emissao', value: 'data_emissao' },
                
        ],

        values: [],


  }),

methods:
{
    api_buscaDadosLog(jobname, company, code, newValue, pageCount)
    {       //#! checar a questao de fazer mesma busca mais de uma vez
            
            var myThat = this

            //myThat.v_checkDouble = true

            myThat.values = []
           
            if (myThat.props_messageType == 'E' && myThat.v_checkDouble === false)
            {   myThat.v_checkDouble = true
                myThat.api_buscaLogsErro(jobname, company, code, newValue, pageCount)
            }
            
            if (myThat.props_messageType == 'S' && myThat.v_checkDouble === false)
            {   myThat.v_checkDouble = true
                myThat.api_buscaEntradadasEncontradas(jobname, company, code, newValue, pageCount)
            }

    },

    
    api_buscaLogsErro(jobname, company, code,  inPage, counter)
    {
        var that = this
        
        
        if(typeof(counter) == "undefined")
        {
            const baseURICount = that.$host + `logs(jobname='${jobname}',company='${company}',code='${code}')/navToLogs/$count`

             that.page = 1
             
             that.$http
              .get(baseURICount, 
              {
                  //auth: {
                  //  username: "tjuliocst",
                  //  password: "elitebox" // Bad password
                  //},
              }
              )
              .then(resultData => {


                console.log("Count-Response:")
                console.log(resultData.data)
                
                // cnpj_dest: "99999999000191"
                // cnpj_emit: "60892403007399"
                // company: "7002"
                // data_emissao: "2012-09-20 00:00:00.0000000 +0"
                // id: "31120960892403007399550010000013271897238261"
                // jobname: "TJULIOCST_20201030141634"
                // nnfe: "1327"


                that.pageCount = Math.ceil(parseInt(resultData.data) / that.itemsPerPage)
                

              })
              .catch((error) => {
                console.log("errro carregar buscaStatusLogsError:" + error);
                 that.jobFinishRun = "False"

                that.v_tableloading = false
                that.v_checkDouble  = false

              });

    }
          
          const baseURI = that.$host + `logs(jobname='${jobname}',company='${company}',code='${code}')/navToLogs?$format=json&$skip=${((that.page*that.itemsPerPage)-that.itemsPerPage)}&$top=${that.itemsPerPage}`
          //$format=json&$skip=0&$top=100`;

          

            console.log ("messagetype-buscadadoslog")
            console.log(that.props_messageType)
           
            that.$http
              .get(baseURI, {
                  //auth: {
                  //  username: "tjuliocst",
                  //  password: "elitebox", // Bad password
                  //},
              }
              )
              .then(resultErrors => {

                console.log("resultLogViewerErrosIDS-Response:")
                console.log(resultErrors.data.d)
                
                resultErrors.data.d.results.forEach(element => {
                    that.values.push({"doc": element.doc, "msg": element.msg, "codigo": element.code })
                });

                that.v_checkDouble = false
                that.v_tableloading = false
                

              })
              .catch((error) => {
                console.log("errro carregar buscaStatusJobScheduler:" + error);
                 that.jobFinishRun = "False"

                that.v_tableloading = false
                that.v_checkDouble = false

              });
    },
    
    api_buscaEntradadasEncontradas(jobname, company, code, inPage, counter)
    {

        var that = this
       
        if(typeof(counter) == "undefined")
        {
            const baseURICount = that.$host + `jobHeaders(jobname='${jobname}',company='${company}')/navJobHeadersToJobItems/$count`

            that.page = 1

             that.$http
              .get(baseURICount, 
              {
                  //auth: {
                  //  username: "tjuliocst",
                  //  password: "elitebox" // Bad password
                  //},
              }
              )
              .then(resultData => {


                console.log("Count-Response:")
                console.log(resultData.data)
                
                // cnpj_dest: "99999999000191"
                // cnpj_emit: "60892403007399"
                // company: "7002"
                // data_emissao: "2012-09-20 00:00:00.0000000 +0"
                // id: "31120960892403007399550010000013271897238261"
                // jobname: "TJULIOCST_20201030141634"
                // nnfe: "1327"


                that.pageCount = Math.ceil(parseInt(resultData.data) / that.itemsPerPage)
                //that.v_checkDouble = false
                            


              })
              .catch((error) => {
                console.log("errro carregar buscaStatusJobScheduler:" + error);
                 that.jobFinishRun = "False"


                that.v_tableloading = false
                //that.v_checkDouble = false

              });

    }

              // if(this.page == 1)
              //   {
              //     this.nextPage = 2
              //     this.backPage = 0              
              //   }
              //   else
              //   { this.page = inPage
              //     this.nextPage = (inPage*this.itemsPerPage) + this.itemsPerPage
              //     this.backPage = (inPage*this.itemsPerPage) - this.itemsPerPage
              //   }        
        
        const baseURI = that.$host + `jobHeaders(jobname='${jobname}',company='${company}')/navJobHeadersToJobItems?$format=json&$skip=${((that.page*that.itemsPerPage)-that.itemsPerPage)}&$top=${that.itemsPerPage}`;

        console.log("pageURL")
        console.log(baseURI)

        console.log ("messagetype-entradasEncontradas")
        console.log(that.props_messageType)

        that.$http
              .get(baseURI, 
              {
                  //auth: {
                  //  username: "tjuliocst",
                  //  password: "elitebox" // Bad password
                  //},
              }
              )
              .then(resultData => {


                console.log("resultLogViewerIdEncontrados-Response:")
                console.log(resultData.data.d)
                
                // cnpj_dest: "99999999000191"
                // cnpj_emit: "60892403007399"
                // company: "7002"
                // data_emissao: "2012-09-20 00:00:00.0000000 +0"
                // id: "31120960892403007399550010000013271897238261"
                // jobname: "TJULIOCST_20201030141634"
                // nnfe: "1327"


                resultData.data.d.results.forEach(elementData => {
                    that.values.push( {"company": elementData.company, 
                                       "chave44": elementData.id, 
                                       "nnfe": elementData.nnfe, 
                                       "cnpj_emit": elementData.cnpj_emit, 
                                       "cnpj_dest": elementData.cnpj_dest, 
                                       "data_emissao" : elementData.data_emissao 
                                       }
                                    )

                });

                console.log("resultLogViewerIdEncontrados-Response-values:")
                console.log( this.values )
                that.v_checkDouble = false
                that.v_tableloading = false
                

              })
              .catch((error) => {
                console.log("errro carregar buscaStatusJobScheduler:" + error);
                 that.jobFinishRun = "False"

                that.v_tableloading = false
                //that.v_checkDouble = false

              });

    }
},


  beforeMount() {
  /*  
     if( this.props_messageType == "E" )
     {
         this.api_buscaDadosLog(this.props_jobname, this.props_company, this.props_code)
     }

     if( this.props_messageType == "S" )
     {
         this.api_buscaEntradadasEncontradas(this.props_jobname, this.props_company, this.props_code)
     }
   */
  },


watch: {
    // whenever question changes, this function will run
    props_company: function (newValue, oldValue) {
     
     
       console.log("mudou jobname>")
       console.log(newValue)

       this.v_tableloading = true
       //this.v_checkDouble = true
       this.api_buscaDadosLog(this.props_jobname, this.props_company, this.props_code)

      //this.answer = 'Waiting for you to stop typing...'
      //this.debouncedGetAnswer()
    },

  props_messageType: function (newValue, oldValue) {
       
//        console.log("mudou tipo servico>")
//        console.log(newValue)

                    
      this.v_tableloading = true
      //this.v_checkDouble = true
      this.api_buscaDadosLog(this.props_jobname, this.props_company, this.props_code)
      
      //this.answer = 'Waiting for you to stop typing...'
      //this.debouncedGetAnswer()
    },

  page: function (newValue, oldValue) {

        this.v_tableloading = true
        //this.v_checkDouble = true
        this.api_buscaDadosLog(this.props_jobname, this.props_company, this.props_code, newValue, this.pageCount)
        

        //if( this.props_messageType == "S" )
        //{
        //    this.api_buscaEntradadasEncontradas(this.props_jobname, this.props_company, this.props_code, newValue, this.pageCount)
        //}

        
       // this.api_buscaEntradadasEncontradas(this.props_jobname, this.props_company, this.props_code, newValue, this.pageCount)

    }

    
}

}



</script>

