<template>
  <v-container>
    <v-row>
      <v-col
        cols="12"
        lg="6"
      >
        <v-menu
          ref="menu1"
          v-model="menu1"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date_b"
              label="Data (De)"
              hint="Formato YYYY-MM-DD"
              persistent-hint
              prepend-icon="mdi-calendar"
              v-bind="attrs"             
              v-on="on"
               readonly
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date_b"
            no-title
            @input="menu1 = false; setDateVuex()"
          ></v-date-picker>
        </v-menu>
        <p>Data da Consulta: <strong>{{ this.date_b }}</strong></p>
      </v-col>

      <v-col
        cols="12"
        lg="6"
      >
      <v-menu
          ref="menu2"
          v-model="menu2"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="date_e"
              label="Data (Até)"
              hint="Formato YYYY-MM-DD"
              persistent-hint
              prepend-icon="mdi-calendar"
              v-bind="attrs"
              v-on="on"
               readonly
            ></v-text-field>
          </template>

          <v-date-picker
            v-model="date_e"
            no-title
            @input="menu2  = false; setDateVuex()"
          ></v-date-picker>
        </v-menu>

        <!--v-menu 
          v-model="menu2"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          max-width="290px"
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="computedDateFormatted"
              label="Date (read only text field)"
              hint="MM/DD/YYYY format"
              persistent-hint
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date_e"
            no-title
            @input="menu2 = false"
          ></v-date-picker>
        </v-menu-->
        <p>Data da Consulta: <strong>{{ date_e }}</strong></p>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
    name: 'CalendarSelect',


    data: vm => ({
      date_b: new Date().toISOString().substr(0, 10),
      date_e: new Date().toISOString().substr(0, 10),
      dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
      menu1: false,
      menu2: false,
    }),

    computed: {
      computedDateFormatted_b () {
        return this.formatDate(this.date_b)
      },
      computedDateFormatted_e () {
        return this.formatDate(this.date_e)
      },
    },

    watch: {
      date_b (val) {
        this.dateFormatted = this.formatDate(this.date_b)
      },
      date_e (val) {
        this.dateFormatted = this.formatDate(this.date_e)
      },

    },

    methods: {
      formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (date) {
        if (!date) return null  

        const [day, month, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
      setDateVuex ()
      {
       
          this.$store.dispatch('nfe_setFiltrosDataInicioConsulta', this.date_b )
      
          this.$store.dispatch('nfe_setFiltrosDataFimConsulta', this.date_e )

          console.log("data> inicio:" + this.$store.getters.nfe_getFiltrosDataInicioConsulta + " / datafim:" + this.$store.getters.nfe_getFiltrosDataFimConsulta)
        
      }
    },
  }
</script>

