import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import axios from 'axios'
import '@babel/polyfill'
import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@mdi/font/css/materialdesignicons.css'

Vue.config.productionTip = false
var urlbase = ""
var ambiente = "HOMOLOGAÇÃO"

axios.interceptors.response.use(function (response) {

  //console.log(response.data)
  try {
    
      if(response.data.trim().indexOf("SAMLRequest") !== -1)
      {
        store.dispatch('nfe_setTimeout', true)
      }
    
  } catch (error) {
    return response;
  }
 
  return response;
}, function (error) {

    
    if (403 === error.response.status || 401 === error.response.status) {

      store.dispatch('nfe_setTimeout', true)

  } else {
    return Promise.reject(error);
  }
});


Vue.prototype.$http = axios


if (window.location.href.toLowerCase().indexOf('localhost') > 0)
{
  urlbase = 'https://fioridev.votorantim.com.br/sap/opu/odata/sap/ZGWBC_NFE_WEBAPP_SRV/'
  //urlbase = 'https://fioridev.votorantim.com.br/sap/opu/odata/sap/ZGWBC_NFE_WEBAPP_FIORI_SRV/'
}
else 
{
  urlbase = '/sap/opu/odata/sap/ZGWBC_NFE_WEBAPP_SRV/'

}

Vue.prototype.$host = urlbase

//KEEP DEV PARAMETERS
Vue.config.devtools = true

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
