<template>

<div>

  <v-card flat>
    <v-toolbar color="grey darken-3" extended flat>
      <!--v-app-bar-nav-icon></v-app-bar-nav-icon-->
    </v-toolbar>

    <v-card class="mx-auto" max-width="90%" style="margin-top: -64px;">
      <v-toolbar flat>
        <v-toolbar-title class="black--text"> DOWNLOAD - {{ c_showName }} ( Data-Hora Criação: {{formatDate(navJobHeadersToJob.date_last_modif)}} - {{formatTime(navJobHeadersToJob.time_last_modif)}} ) </v-toolbar-title>
      </v-toolbar>
        <v-spacer></v-spacer>
        <v-card elevation="2">
          <!--p style="padding-left:50px;"><span style="color:red"><b>Atenção:</b></span> Os arquivos ZIP dos XMLs extraidos serão deletados 10 dias após sua disponibilização. Programe-se para fazer a extração dentro desse período.</p-->
        </v-card>
        <!-- input criterias -->
        <!--v-data-table
          :headers="headers"
          :items="values"
          item-key="name"
          sort-by="name"
          group-by="category"
          class="elevation-1"
          show-group-by   
          single-select       
        ></v-data-table-->

      <v-card-text>
        <!--MACRO DETAILS -->
       
            <v-container fluid>
                <v-data-iterator
                  :items="items"
                  :items-per-page.sync="itemsPerPage"
                  :page="page"
                  :search="search"
                  :sort-by="sortBy.toLowerCase()"
                  :sort-desc="sortDesc"
                  hide-default-footer
                >
                  <template v-slot:header>
                    <v-toolbar
                      dark
                      color="blue darken-3"
                      class="mb-1"
                    >
                      <v-text-field
                        v-model="search"
                        clearable
                        flat
                        solo-inverted
                        hide-details
                        prepend-inner-icon="mdi-file-find"
                        label="Buscar por Empresas"
                      ></v-text-field>
                      <template v-if="$vuetify.breakpoint.mdAndUp">
                        <v-spacer></v-spacer>
                        <v-select
                          v-model="sortBy"
                          flat
                          solo-inverted
                          hide-details
                          :items="keys"
                          prepend-inner-icon="mdi-sort"
                          label="Ordenar por"
                        ></v-select>
                        <v-spacer></v-spacer>
                        <v-btn-toggle
                          v-model="sortDesc"
                          mandatory
                        >
                          <v-btn
                            large
                            depressed
                            color="blue"
                            :value="false"
                          >
                            <v-icon>mdi-arrow-up</v-icon>
                          </v-btn>
                          <v-btn
                            large
                            depressed
                            color="blue"
                            :value="true"
                          >
                            <v-icon>mdi-arrow-down</v-icon>
                          </v-btn>
                        </v-btn-toggle>
                         <v-spacer></v-spacer>

                        <v-chip label large @click="api_downloadCSV($route.params.id_job , '*' )">  TOTAL: {{totalGeral}} </br> baixar csv</v-chip>
                      </template>
                    </v-toolbar>
                  </template>

                  <template v-slot:default="props">
                    <v-row>
                      <v-col
                        v-for="item in props.items"
                        :key="item.empresa"
                        cols="4"
                      >
                        <v-card>
                          <v-card-title class="headline">
                            <v-row no-gutters>
                               <v-chip color="primary" large style="width: 50%; font-size: large;" outlined> {{ item.empresa }} </v-chip> <v-spacer></v-spacer>
                              <v-chip color="primary" large style="text-align: right; "> <a @click="api_downloadCSV($route.params.id_job , item.empresa )" style="color:white;" > Baixar CSV </a> </v-chip>
                               
                             
                            </v-row>
                            <!--v-chip><v-icon>mdi-plus-box-outline</v-icon></v-chip-->
                          </v-card-title>
                          
                     
                          <v-list>
                            <v-list-item
                              v-for="(key, index) in filteredKeys"
                              :key="index"
                            >        
                            <v-row no-gutters>       

                              <v-col cols="5"><v-list-item-content :class="{ 'blue--text': sortBy === key }" style="font-size: large;">{{ key }}:</v-list-item-content></v-col>
                              <v-col cols="12">
                              <v-list-item-content class="title align-begin" :class="{ 'blue--text': sortBy === key }">
                                <v-list-item-content class="title align-begin" v-if="key.toLowerCase() === 'arquivo'"> <span style="font-size: 1.1vw;"> {{ item[key.toLowerCase()] }} </span> </v-list-item-content> 
                                <v-list-item-content class="title align-begin" v-else-if="key.toLowerCase() === 'mediafile'" style="text-align: left;">
                                    <a @click="startDownloadWindow(item[key.toLowerCase()])"> <v-icon>mdi-folder-download</v-icon> Download XMLs </a>
                                </v-list-item-content class="title align-begin">
                                <v-list-item-content v-else-if="key.toLowerCase() === 'sequencia'"> {{ item[key.toLowerCase()] }} </v-list-item-content>
                              </v-list-item-content>
                              </v-col>
                            </v-row>
                            </v-list-item>
                          </v-list>
                        </v-card>
                      </v-col>
                    </v-row>
                  </template>

                  <template v-slot:footer>
                    <v-row class="mt-2" align="center" justify="center">
                      <span class="grey--text">Items per page</span>
                      <v-menu offset-y>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            dark
                            text
                            color="primary"
                            class="ml-2"
                            v-bind="attrs"
                            v-on="on"
                          >
                            {{ itemsPerPage }}
                            <v-icon>mdi-chevron-down</v-icon>
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            v-for="(number, index) in itemsPerPageArray"
                            :key="index"
                            @click="updateItemsPerPage(number)"
                          >
                            <v-list-item-title>{{ number }}</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>

                      <v-spacer></v-spacer>

                      <span
                        class="mr-4
                        grey--text"
                      >
                        Page {{ page }} of {{ numberOfPages }}
                      </span>
                      <v-btn
                        fab
                        dark
                        color="blue darken-3"
                        class="mr-1"
                        @click="formerPage"
                      >
                        <v-icon>mdi-chevron-left</v-icon>
                      </v-btn>
                      <v-btn
                        fab
                        dark
                        color="blue darken-3"
                        class="ml-1"
                        @click="nextPage"
                      >
                        <v-icon>mdi-chevron-right</v-icon>
                      </v-btn>
                    </v-row>
                  </template>
                </v-data-iterator>


                        <!--div style="width:100%; display:flex; padding:30px; justify-content:center;"-->
                            <!--v-btn large color="primary" @click="showTable=!showTable" width="80%">Schedular Download</v-btn-->
                        <!--/div-->
              </v-container>

      </v-card-text>

<!-- FINAL MACRO DETAILS -->

   <v-divider></v-divider>

    </v-card>
  </v-card>
</div>

</template>

<script>
export default {

props: {
      //id_job: String
    },

  name: 'app',
data: (vm) => ({
    opt_empresa: [],
    opt_value_empresa: [],

    headers: [
          {
            text: 'Critérios de Filtro utilizados',
            align: 'start',
            value: 'name',
            groupable: true,
          },
          { text: 'ID',  value: 'category', align: 'right' },
          { text: 'NFE', value: 'dairy', align: 'right' },
        ],
        values: [],

        navJobHeadersToJob : {
            "__metadata": {
                "id": "",
                "uri": "",
                "type": ""
            },
            "jobname": "",
            "date_created": "",
            "time_created": "",
            "user_created": "",
            "status": "",
            "title": "",
            "description": "",
            "date_last_modif": "",
            "time_last_modif": "",
            "user_last_modif": "",
            "doc_type": ""
        },
      
    
    itemsPerPageArray: [3, 6, 12],
    search: "",
    filter: {},
    sortDesc: false,
    page: 1,
    itemsPerPage: 6,
    sortBy: "Empresa",
    keys: ["Empresa", "Arquivo", "Sequencia", "MediaFile"],
    items: [],
    totalGeral: 0
    
    }),

  methods: {

formatDate (date, spacer) {
        if (!date) return null

        const year = date.substr(0,4)
        const month = date.substr(4,2)
        const day = date.substr(6,2) 
        
        if (!spacer)
          return `${day}/${month}/${year}`
        else  
          return `${year} ${month} ${day}` 
  },

  formatTime (time, spacer) {
        if (!time) return null

        const hour = time.substr(0,2)
        const minutes = time.substr(2,2)
        const seconds = time.substr(4,2) 
        
        if (!spacer)
          return `${hour}:${minutes}:${seconds}`
        else  
          return `${hour} ${minutes} ${seconds}` 
  },

    fetchEmpresas: function () {
          
      const baseURI = this.$host + 'companies?$format=json'
      this.$http.get(baseURI, 
       {
              //auth: {
              //  username: 'tjuliocst',
              //  password: 'elitebox' // Bad password
              //}
       }
  )
      .then((result) => {
         var empresas = [];
          result.data.d.results.forEach((empresa) => {
            empresas.push(empresa.bukrs);
            this.opt_value_empresa.push(empresa.bukrs);
          });

          this.$store.dispatch("nfe_setEmpresas", empresas);
      })
    },

    api_downloadCSV(jobname, empresa_id)
    {
      const baseURI = this.$host + `jobItems(jobname='${jobname}',company='${empresa_id}')/$value`

      this.startDownloadWindow(baseURI)

    },


    api_buscaDetalhesJobScheduler(job_id)
    {
            const baseURI = this.$host + `jobHeaders(jobname='${job_id}',company='*')?$expand=navJobHeadersToJob,navJobHeaderToFiles&$format=json`;
          //const baseURI = this.$host + `/files?$filter=jobname eq '${job_id}' and company eq '*'&$format=json`; 


            var navJobHeaderToJobHeaders =
                            {
                                    "results": [
                                        {
                                            "__metadata": {
                                                "id": "",
                                                "uri": "",
                                                "type": ""
                                            },
                                            "jobname": "",
                                            "company": "",
                                            "total": "",
                                            "docsFound": "",
                                            "notFound": "",
                                            "status": "",
                                            "msg": "",
                                            "navJobHeaderToJobLogs": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeadersToJobFilters": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeaderToJobHeaders": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeadersToJobItems": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeaderToFiles": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            }
                                        }
                                    ]
                                }
            
            var navJobHeadersToJobFilters = 
                              {
                                "results": [
                                    {
                                        "__metadata": {
                                            "id": "",
                                            "uri": "",
                                            "type": ""
                                        },
                                        "jobname": "",
                                        "fieldname": "",
                                        "de": "",
                                        "ate": ""
                                    } ]
                              }

            var navJobHeaderToFiles = 
                          {
                          "results": [
                              {
                                  "__metadata": {
                                      "id": "",
                                      "uri": "",
                                      "type": "",
                                      "content_type": "",
                                      "media_src": ""
                                  },
                                  "jobname": "",
                                  "company": "",
                                  "seq": "",
                                  "Mimetype": "",
                                  "fileName": ""
                              } ]
                          }

           
            this.$http
              .get(baseURI, 
               {
                  //auth: {
                  //username: "tjuliocst",
                  //password: "elitebox", // Bad password
                  //},
               }
              ).then((result) => {
                //console.log("resultJobResponse:")

               //navJobHeadersToJobFilters = result.data.d
                //console.log("Filters>")
                //console.log(navJobHeadersToJobFilters)

                //navJobHeadersToJobFilters.results.forEach((filtro) => {
                //  var l_name = ((filtro.ate != "")?'-':'') + filtro.ate
                //  this.values.push({name:`${filtro.de} ${l_name}`, category: filtro.fieldname, dairy: ''})
                
                //})

                console.log("navJobHeadersToJob>")
                this.navJobHeadersToJob = result.data.d.navJobHeadersToJob
                console.log(this.navJobHeadersToJob) 

                navJobHeaderToFiles  = result.data.d.navJobHeaderToFiles
                console.log("LinksToDownloadDataResponse>")
                //console.log(navJobHeaderToFiles)
                
                navJobHeaderToFiles.results.forEach((resumo) => {
                  //"Empresa", "Arquivo", "Sequencia", "MediaFile"
                  //this.items.push({empresa:resumo.company, arquivo: resumo.fileName , sequencia: resumo.seq, mediafile: resumo.__metadata.media_src})
                  
                  this.items.push({empresa:resumo.company, arquivo: resumo.fileName , sequencia: resumo.seq, 
                  mediafile: this.$host + `files(jobname='${resumo.jobname}',company='${resumo.company}',seq='${resumo.seq}')/$value`
                  })
                  this.totalGeral = parseInt(this.totalGeral) + 1

                })

                console.log(this.items)
				


                return true
              })
              .catch((error) => {
                console.log("errro carregar empresas:" + error);
                return false
              });
    },


      nextPage() {
            if (this.page + 1 <= this.numberOfPages) this.page += 1;
          },

          formerPage() {
            if (this.page - 1 >= 1) this.page -= 1;
          },

          updateItemsPerPage(number) {
            this.itemsPerPage = number;
          },

      startDownloadWindow(urlFile)
      {
          window.open(urlFile);
      }

  },

  computed: {
    
    c_showName() {
          if (this.navJobHeadersToJob.title == "")
          {return 'N/A';}
          else
          {return this.navJobHeadersToJob.title;}
    },

    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    
    filteredKeys() {
      return this.keys.filter((key) => key !== `Empresa`);
    },
    
    get_id_job()
    {
      return this.$route.params.id_job;
    }
    },

   
    beforeMount() {

    if(typeof(this.$store.getters.nfe_getFiltrosEmpresa) == 'undefined' || this.$store.getters.nfe_getFiltrosEmpresa.length == 0)
    {
          this.fetchEmpresas()          
    }

    this.api_buscaDetalhesJobScheduler(this.$route.params.id_job)



  }


}
</script>

<style scoped>


</style>    