<script>
export default {
  name: 'NfeDialog',

props: { 
    dialog_chave44:Boolean,
    dialog_chavenf:Boolean,
    dialog_nfe_empresa:Boolean,
    dialog_nfe_cnpj_emissor: Boolean,
    dialog_nfe_cnpj_destinario: Boolean,
    dialog_nfe_empresa: Boolean,

