    dialog_cnpj_recebedor: Boolean,
    dialog_cnpj_tomador: Boolean,
  

    props_chave44: String,
    props_chavenf: String,
    props_cnpj_emissor: String,
    props_cnpj_destinatario: String,

    props_cnpj_remetente: String,
    props_cnpj_recebedor: String,
    props_cnpj_tomador: String

},

data: () => ({ 

  items_chave44: [
