/* Metodos da parte Chave44 */

     inputAddField(index, inputFieldName) {
        var v_check = ''
        switch (inputFieldName){ 
          case 'items_chave44':
                try {
                      v_check = this.items_chave44[index+1].fieldname.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_chave44.push({ 'fieldname': '' });
                  console.log(this.items_chave44)
              break
          
          case 'items_chavenf':
                try {
                      v_check = this.items_chavenf[index+1].fieldname.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_chavenf.push({ 'fieldname': '' });
                  console.log(this.items_chavenf)
              break


          case 'items_cnpj_emissor':
                try {
                      v_check = this.items_cnpj_emissor[index+1].de.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_cnpj_emissor.push({ 'de':'', 'ate':''});
                  console.log("cnpj emissor>" + this.items_cnpj_emissor)
              break

          case 'items_cnpj_destinatario':
                try {
                      v_check = this.items_cnpj_destinatario[index+1].de.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_cnpj_destinatario.push({ 'de':'', 'ate':''});
                  console.log(this.items_cnpj_destinatario)
              break
              
          
          case 'items_cnpj_remetente':
                try {
                      v_check = this.items_cnpj_remetente[index+1].de.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_cnpj_remetente.push({ 'de':'', 'ate':''});
                  console.log(this.items_cnpj_remetente)
              break
          
          case 'items_cnpj_recebedor':
                try {
                      v_check = this.items_cnpj_recebedor[index+1].de.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_cnpj_recebedor.push({ 'de':'', 'ate':''});
                  console.log(this.items_cnpj_recebedor)
              break
                 
          case 'items_cnpj_tomador':
                try {
                      v_check = this.items_cnpj_tomador[index+1].de.indexOf('')
                    }
                    catch(e) {
                      v_check = 'undefined'
                    }
                            
                if ( v_check == 'undefined' )
                  this.items_cnpj_tomador.push({ 'de':'', 'ate':''});
                  console.log(this.items_cnpj_tomador)
              break

                 }
       
        },
     
     inputRemoveField(index, inputFieldName) {
        var v_check = ''
        switch (inputFieldName){ 
           case 'items_chave44':
                        this.items_chave44.splice(index, 1);
            break
           case 'items_chavenf':
                        this.items_chavenf.splice(index, 1);
            break
           case 'items_cnpj_emissor':
