            break
          case 'items_cnpj_remetente':
                        this.items_cnpj_remetente.splice(index, 1);
            break
          case 'items_cnpj_recebedor':
                        this.items_cnpj_recebedor.splice(index, 1);
            break
          case 'items_cnpj_tomador':
                        this.items_cnpj_tomador.splice(index, 1);
            break
        }
                    },
              

     
    save(inputFieldName)
    {   
       switch (inputFieldName){ 
        case 'items_chave44':
          var len = this.items_chave44.length
          for (let index = 0; index < len; index++) 
          {

            if (len > 1 && (this.items_chave44[index].fieldname.toString() == '') )
            {
                this.items_chave44.splice(index,1)
                console.log("chave44-remove:" + this.items_chave44.length)
            }
            
          }
                  
          this.$store.dispatch( 'nfe_setChaves44', this.items_chave44 )
          console.log(this.items_chave44)
          this.$emit('closemodal')
          break

        case 'items_chavenf':
