<template>

    <div>
      <v-card elevation="2">
      <v-card-title>Legendas:</v-card-title>
      <v-divider class="mx-4"></v-divider>
      <v-row class="text-center" justify="center">
        <v-col class="border: black;  ">
         <div
            class="pa-4 yellow darken-3 rounded-circle d-inline-block"
          ></div>

          <div>Processando seleção do usuário</div>
        </v-col>
        <v-col class="border: black;  ">
         <div
            class="pa-4 orange darken-3 rounded-circle d-inline-block"
          ></div>

          <div>Seleção do usuário finalizado.</div>
        </v-col>
        <v-col>
          <div
            class="pa-4 blue darken-3 rounded-circle d-inline-block"
          ></div>

          <div>Processando Download.</div>
        </v-col>
        <v-col>
          <div
            class="pa-4 green darken-3 rounded-circle d-inline-block"
          ></div>

          <div>Download Finalizado.</div>
        </v-col>
        <v-col>
          <div
            class="pa-4 purple darken-3 rounded-circle d-inline-block"
          ></div>

          <div>Arquivos ZIP deletados.</div>
        </v-col>
         <v-col>
          <div
            class="pa-4 red darken-3 rounded-circle d-inline-block"
          ></div>

          <div>Erro no processamento JOB/Serviço.</div>
        </v-col>
      </v-row>
      </v-card>
    <v-timeline dense> 

     <v-container >
       
      <v-data-iterator 
        :items="items"
        items-per-page.sync="itemsPerPage"
        :page="page"
        :search="search"
        :sort-by="sortBy.toLowerCase()"
        :sort-desc="sortDesc"
        :loading="v_loadingFullPage"
         loading-text="Carregando dados - A G U A R D E ..."
        hide-default-footer
      >

        <template v-slot:header>
                    <v-toolbar
                      dark
                      color="blue-grey darken-3"
                      class="mb-1"
                    >
                      <v-text-field
                        v-model="search"
                        clearable
                        flat
                        solo-inverted
                        hide-details
                        prepend-inner-icon="mdi-file-find"
                        label="Buscar por palavras"
                      ></v-text-field>
          
                    </v-toolbar>
        </template>
      
        <template v-slot:default="props">

                <v-row v-for="item in props.items"
                 :key="item.jobname"> 
                 
                 
                 <v-col>

                  <v-list dense>
                    <v-list-item 
                    v-for="(key, index) in filteredKeys"
                              :key="index"
                              >
                  
                  <v-list-item-content>

                  <v-card max-width="98%" elevation="2">

                  <v-card-title class="blue-grey darken-2">
                                    
                                                         
                                    <v-tooltip top :color="showColor(item.status)"  >
                                      <template v-slot:activator="{ on, attrs }">

                                                <v-icon
                                                  dark
                                                  size="42"
                                                  class="mr-4"
                                                  :color="showColor(item.status)"
                                                  v-bind="attrs" v-on="on"
                                                >
                                                  mdi-checkbox-blank-circle
                                                </v-icon>

                                        </template>
                                        <span>{{item.status_desc}}</span>
                                    </v-tooltip>

                                    <h4 v-if="item.status == 'F'" class="white--text font-weight-light"><b style="font-size:x-large;">{{item.title}} </b> <br> Criado pelo usuário: {{item.user_created}} <br> Data de Criação: {{formatDate(item.date_created)}}</h4>
                                   
                                    <h4 v-else class="white--text font-weight-light"><b style="font-size:x-large;">{{item.title}} </b> {{(item.date_delete_file != "00000000" )?` - Arquivo ZIP expira dia ${formatDate(item.date_delete_file)} (Faltam ${dateDiffInDays(item.date_delete_file, "spacer")} dias)`:""}} <br> Criado pelo usuário: {{item.user_created}} <br> Data de Criação: {{formatDate(item.date_created)}}</h4>
                  </v-card-title>

                             
                                  <v-container>
                                    <v-row >
                                      <v-col v-if="item.status == 'D'">
                                        
                                          {{item.description}}<br><br>
                                            <router-link :to="{path:`/download/${item.jobname}`}">+ Detalhes</router-link>
                                      </v-col>
                                      <v-col v-else-if="item.status == 'B'">
                                          {{item.description}}<br><br>
                                            <router-link :to="{path:`/details/${item.jobname}`}">+ Detalhes</router-link>
                                      </v-col>
                                      <v-col v-else-if="item.status == 'E'">
                                          {{item.description}}<br><br>
                                          Causa do erro: {{(item.error_desc === "")?" Não foi possível definir.":item.error_desc}}
                                      </v-col>
                                       <v-col v-else>
                                          {{item.description}}
                                            
                                      </v-col>
                                      <v-col
                                        class="hidden-sm-and-down text-left"
                                        md="2"
                                      >
                                      
                                      </v-col>
                                    </v-row>
                                  </v-container>
                </v-card>
                </v-list-item-content>

                                  </v-list-item>
                                  </v-list>

                    

                            
                            
                          
              
              </v-col>
              </v-row>
           

        </template>

                  <template v-slot:footer>
                    <v-row class="mt-2" align="center" justify="center">
                      <!--
                      <span class="grey--text">Items por página</span>
                      <v-menu offset-y>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            dark
                            text
                            color="primary"
                            class="ml-2"
                            v-bind="attrs"
                            v-on="on"
                          >
                            {{ itemsPerPage }}
                            <v-icon>mdi-chevron-down</v-icon>
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            v-for="(number, index) in itemsPerPageArray"
                            :key="index"
                            @click="updateItemsPerPage(number)"
                          >
                            <v-list-item-title>{{ number }}</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>

                      <v-spacer></v-spacer>
                      -->
                      <span
                        class="mr-4
                        grey--text"
                      >
                        Página {{ page }} de {{ numberOfPages }}
                      </span>
                      <v-btn
                        fab
                        dark
                        color="blue darken-3"
                        class="mr-1"
                        @click="formerPage"
                      >
                        <v-icon>mdi-chevron-left</v-icon>
                      </v-btn>
                      <v-btn
                        fab
                        dark
                        color="blue darken-3"
                        class="ml-1"
                        @click="nextPage"
                      >
                        <v-icon>mdi-chevron-right</v-icon>
                      </v-btn>
                    </v-row>
                  </template>

                </v-data-iterator>

 
      </v-container>


     </v-timeline>
    </div>
</template>
<script>
export default {

data: () => ({
    
    jobdetails: [],
    v_loading: true ,
    v_loadingFullPage: true,

    itemsPerPageArray: [4, 8, 16],
    search: "",
    filter: {},
    sortDesc: false,
    page: 1,
    itemsPerPage: 4,
    sortBy: "",
    keys: ["Jobname", ""],
    items: [],
  }),

methods:{

   formatDate (date, spacer) {
        if (!date) return null

        const year = date.substr(0,4)
        const month = date.substr(4,2)
        const day = date.substr(6,2) 
        
        if (!spacer)
          return `${day}/${month}/${year}`
        else  
          return `${year} ${month} ${day}` 
  },

  dateDiffInDays (dataDoc)
  {

      const _MS_PER_DAY = 1000 * 60 * 60 * 24;
      const a = new Date(Date.now())
      const b = new Date(this.formatDate(dataDoc, "spacer"))
      // a and b are javascript Date objects
     
        // Discard the time and time-zone information.
        const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
        const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());
        var difference =  Math.floor((utc2 - utc1) / _MS_PER_DAY);
     
      // test it
      
         // b = new Date("2017-07-25"),
         // difference = dateDiffInDays(a, b);
         return difference

  },

  getJobDetails: function () {
          
      const baseURI = this.$host + 'jobs?$skip=0&$top=150&$orderby=jobname&$format=json'
      this.$http.get(baseURI, 
       {
             //auth: {
             //  username: 'tjuliocst',
             // password: 'elitebox' // Bad password
             // }
       }
  )
      .then((result) => {
        
         var jobdetail =  {
                "__metadata": {
                    "id": "",
                    "uri": "",
                    "type": ""
                },
                "jobname": "",
                "date_created": "",
                "time_created": "",
                "user_created": "",
                "status": "",
                "title": "",
                "description": "",
                "date_last_modif": "",
                "time_last_modif": "",
                "user_last_modif": "",
                "doc_type": "",
                "date_delete_file": "",
            }


          result.data.d.results.forEach((detail) => {

          var l_statusdesc = ""

          switch (detail.status) {
            case "D":
              l_statusdesc =  "Download Finalizado."
              break;
            
            case "C":
              l_statusdesc = "Processando Download."
              break;
          
            case "B":
              l_statusdesc = "Seleção do usuário finalizado."
              break;
            
            case "A":
              l_statusdesc = "Seleção do usuário finalizado."
              break;    

            case "F":
              l_statusdesc = "Arquivos ZIP deletados."
              break; 


            default:
              l_statusdesc = "Erro no processamento JOB/Serviço."
              break;
          }


            this.jobdetails.push({
              
                "jobname": detail.jobname,
                "date_created": detail.date_created,
                "time_created": detail.time_created,
                "user_created": detail.user_created,
                "status": detail.status,
                "title": detail.title,
                "description": detail.description,
                "date_last_modif": detail.date_last_modif,
                "time_last_modif": detail.time_last_modif,
                "user_last_modif": detail.user_last_modif,
                "doc_type": detail.doc_type,
                "status_desc" : l_statusdesc,
                "date_delete_file" : detail.date_delete_file,
                "error_desc" : detail.error_desc
            })

          });


            //console.log("SummaryResults>")
            //console.log(this.jobdetails)
            this.items = this.jobdetails

            this.v_loadingFullPage = false
         
      })
    },

     nextPage() {
          if (this.page + 1 <= this.numberOfPages) this.page += 1;
          },

          formerPage() {
            if (this.page - 1 >= 1) this.page -= 1;
          },

          updateItemsPerPage(number) {
            this.itemsPerPage = number;
          },

      showColor(inColor)
      {
          switch (inColor) {
            case "D":
              return "green darken-3"
              break;
            
            case "C":
              return "blue darken-3"
              break;
          
            case "B":
              return "orange darken-3"
              break;

            case "A":
              return "yellow darken-3"
              break;

            case "F":
              return "purple darken-3"
              break;

            default:
              return "red darken-3"
              break;
          }
      }

},

computed: {
    

    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },

     filteredKeys() {
      return this.keys.filter((key) => key !== `Jobname`);
    },
    
    get_id_job()
    {
      return this.$route.params.id_job;
    }
    
},

beforeMount() 
{

    this.getJobDetails()

  }


}
</script>

<style scoped>

:root {
  --timeline-opposite-item-width: 160px;
  --timeline-line-width: 8px;
}

.v-timeline--dense .v-timeline-item__opposite {
  display: inline-block;
}

.v-timeline-item__opposite {
  flex: none;
  min-width: var(--timeline-opposite-item-width);
}

/* line: divider in the middle is 96px wide by default */
.v-application--is-ltr .v-timeline--dense:not(.v-timeline--reverse):before {
  left: calc(
    var(--timeline-opposite-item-width) + 
    (96px - var(--timeline-line-width)) / 2
  );
  width: var(--timeline-line-width);
}

</style>
