import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    filtros: {
      nfe_chave44: [],
      nfe_chavenf: [],
      nfe_empresa: [],
      nfe_cnpj_emissor: [],
      nfe_cnpj_destinario: [],
      nfe_data_inicio: '',
      nfe_data_fim: '',

      nfe_rem_cnpj: [],
      nfe_recebe_cnpj:[],
      nfe_toma_cnpj:[]

    },
    csrf_token:'',
    checkTimeout: false,

  },

  getters: {
    nfe_getTimeout(state) {
      return state.checkTimeout
    },
    nfe_getFiltrosChave44(state) {
      return state.filtros.nfe_chave44
    },
    nfe_getFiltros(state) {
      return state.filtros.nfe_chave44
    },
    nfe_getFiltrosChaveNf(state) {
      return state.filtros.nfe_chavenf
    },
    nfe_getFiltrosEmpresa(state) {
      return state.filtros.nfe_empresa
    },
    nfe_getFiltrosNfeEmissor(state) {
      return state.filtros.nfe_cnpj_emissor
    },
    nfe_getFiltrosNfeDestinatario(state) {
      return state.filtros.nfe_cnpj_destinario
    },
    nfe_getFiltrosDataInicioConsulta(state) {
      return state.filtros.nfe_data_inicio
    },
    nfe_getFiltrosDataFimConsulta(state) {
      return state.filtros.nfe_data_fim
    },
    nfe_getCsrfToken(state) {
      return state.csrf_token
    },

    nfe_getFiltrosCteRemCNPJ(state) {
      return state.filtros.nfe_rem_cnpj
    },
    nfe_getFiltrosCteRecebedorCnpj(state) {
      return state.filtros.nfe_recebe_cnpj
    },
    nfe_getFiltrosCteTomadorCnpj(state) {
      return state.filtros.nfe_toma_cnpj
    }
  },

  mutations: {
    nfe_setTimeout(state, timeout) {
      this.state.checkTimeout = timeout
    },
    nfe_setFiltrosChave44(state, chaves44) {
      this.state.filtros.nfe_chave44 = chaves44
    },
    nfe_setFiltrosChaveNf(state, chavenf) {
      this.state.filtros.nfe_chavenf = chavenf
    },
    nfe_setFiltrosEmpresa(state, empresas) {
      this.state.filtros.nfe_empresa = empresas
    },
    nfe_setFiltrosNfeEmissor(state, cnpjs) {
      this.state.filtros.nfe_cnpj_emissor = cnpjs
    },
    nfe_setFiltrosNfeDestinatario(state, cnpjs) {
      this.state.filtros.nfe_cnpj_destinario = cnpjs
    },
    nfe_setFiltrosDataInicioConsulta(state, data) {
      this.state.filtros.nfe_data_inicio = data
    },
    nfe_setFiltrosDataFimConsulta(state, data) {
      this.state.filtros.nfe_data_fim = data
    },
    nfe_setCsrfToken(state, data) {
      this.state.csrf_token = data
    },

    nfe_setFiltrosCteRemCNPJ(state, nfe_rem_cnpj) {
      this.state.filtros.nfe_rem_cnpj = nfe_rem_cnpj
    },
    nfe_setFiltrosCteRecebedorCnpj(state, nfe_recebe_cnpj) {
      this.state.filtros.nfe_recebe_cnpj = nfe_recebe_cnpj
    },
    nfe_setFiltrosCteTomadorCnpj(state, nfe_toma_cnpj) {
      this.state.filtros.nfe_toma_cnpj = nfe_toma_cnpj
    },


  },

  actions: {
    
    nfe_setTimeout(commit, timeout) {
      this.commit('nfe_setTimeout', timeout)
    },
    nfe_setChaves44(commit, chaves44) {
      this.commit('nfe_setFiltrosChave44', chaves44)
    },
    nfe_setChavesNf(commit, chavesnf) {
      this.commit('nfe_setFiltrosChaveNf', chavesnf)
    },
    nfe_setEmpresas(commit, empresas) {
      this.commit('nfe_setFiltrosEmpresa', empresas)
    },
    nfe_setNfeEmissor(commit, cnpjs) {
      this.commit('nfe_setFiltrosNfeEmissor', cnpjs)
    },
    nfe_setNfeDestinatario(commit, cnpjs) {
      this.commit('nfe_setFiltrosNfeDestinatario', cnpjs)
    },
    nfe_setFiltrosDataConsulta(commit, data) {
      this.commit('nfe_setFiltrosDataConsulta', data)
    },
    nfe_setFiltrosDataInicioConsulta(commit, data) {
      this.commit('nfe_setFiltrosDataInicioConsulta', data)
    },
    nfe_setFiltrosDataFimConsulta(commit, data) {
      this.commit('nfe_setFiltrosDataFimConsulta', data)
    },
    nfe_setCsrfToken(commit, data) {
      this.commit('nfe_setCsrfToken', data)
    },

    nfe_setFiltrosCteRemCNPJ(commit, nfe_rem_cnpj) {
      this.commit('nfe_setFiltrosCteRemCNPJ', nfe_rem_cnpj)
    },

    nfe_setFiltrosCteRecebedorCnpj(commit, nfe_recebe_cnpj) {
      this.commit('nfe_setFiltrosCteRecebedorCnpj', nfe_recebe_cnpj)
    },

    nfe_setFiltrosCteTomadorCnpj(commit, nfe_toma_cnpj) {
      this.commit('nfe_setFiltrosCteTomadorCnpj', nfe_toma_cnpj)
    },


  },

  modules: {
  }
})
