<template>
  <div>
    <v-content >
    <v-card width="400px" class="mx-auto mt-5">
      <v-card-title>Portal Services</v-card-title>
      <v-card-text>
        <v-form>
          <v-text-field label="Usuário/email" prepend-icon="mdi-account-circle" />
          <v-text-field
            label="Senha"
            :type="showPassword ? 'text' : 'password'"
            prepend-icon="mdi-lock"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="showPassword = !showPassword"
          />
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-btn color="info" to='main'>Acessar</v-btn>
        <v-spacer />
        <v-btn color="gray">Reset de Senha</v-btn>
      </v-card-actions>
    </v-card>
    </v-content>
  </div>
</template>

<script>
export default {
  name: 'login',

  data: () => ({
    showPassword: false
  })
}
</script>
