<template>

<div>

  <v-card flat>
    <v-toolbar color="grey darken-3" extended flat>
      <!--v-app-bar-nav-icon></v-app-bar-nav-icon-->
    </v-toolbar>

    <v-card class="mx-auto" max-width="90%" style="margin-top: -64px;">
      <v-toolbar flat>
        <v-toolbar-title class="black--text">Resultado consulta "{{ c_showName }}" ( Data-Hora Criação:{{formatDate(navJobHeadersToJob.date_last_modif)}} - {{formatTime(navJobHeadersToJob.time_last_modif)}} ) </v-toolbar-title>
      </v-toolbar>
        <v-spacer></v-spacer>
        Critérios de busca utilizados:
        <!-- input criterias -->
        <v-data-table 
          :headers="headers"
          :items="values"
          item-key="name"
          sort-by="name"
          group-by="categoria"
          class="elevation-1"
          show-group-by   
          single-select 
          :loading="v_loadFullPage"
          loading-text="Carregando página - A G U A R D E ... "
                    
        ></v-data-table>
        <v-divider class="mx-7"></v-divider>
      <v-row>
        <v-col></v-col>
        <v-col><v-chip style="color:white" label large color="red darken-3" @click="api_downloadCSV($route.params.id_job , '*', 'error' )">  TOTAL FALTANTES: {{totalGeralErros}} </br> baixar csv</v-chip></v-col>
        <v-col><v-chip style="color:white" label large color="green darken-3" @click="api_downloadCSV($route.params.id_job , '*' )">  TOTAL ENCONTRADOS: {{totalGeral}} </br> baixar csv</v-chip></v-col>
        <v-col></v-col>
      </v-row>
                
      <v-card-text>
        <!--MACRO DETAILS -->
                      
            <v-container fluid>
                <v-data-iterator
                  :items="items"
                  :items-per-page.sync="itemsPerPage"
                  :page="page"
                  :search="search"
                  :sort-by="sortBy.toLowerCase()"
                  :sort-desc="sortDesc"
                  hide-default-footer
                >
                
                  <template v-slot:header>
                    <v-toolbar
                      dark
                      color="blue darken-3"
                      class="mb-1"
                    >
                      <v-text-field
                        v-model="search"
                        clearable
                        flat
                        solo-inverted
                        hide-details
                        prepend-inner-icon="mdi-file-find"
                        label="Buscar por Empresas"
                      ></v-text-field>
                      <template v-if="$vuetify.breakpoint.mdAndUp">
                        <v-spacer></v-spacer>
                        <v-select
                          v-model="sortBy"
                          flat
                          solo-inverted
                          hide-details
                          :items="keys"
                          prepend-inner-icon="mdi-sort"
                          label="Ordenar por"
                        ></v-select>
                        <v-spacer></v-spacer>
                        <v-btn-toggle
                          v-model="sortDesc"
                          mandatory
                        >
                          <v-btn
                            large
                            depressed
                            color="blue"
                            :value="false"
                          >
                            <v-icon>mdi-arrow-up</v-icon>
                          </v-btn>
                          <v-btn
                            large
                            depressed
                            color="blue"
                            :value="true"
                          >
                            <v-icon>mdi-arrow-down</v-icon>
                          </v-btn>
                        </v-btn-toggle>

                       
                        <v-spacer></v-spacer>
                        <h3>TOTAL GERAL: <br> {{totalGeral + totalGeralErros}}</h3>
         
                      </template>
                    </v-toolbar>
                  </template>

                  <template v-slot:default="props">
                    <v-row>
                      <v-col
                        v-for="item in props.items"
                        :key="item.empresa"
                        cols="4"
                      >
                        <v-card>
                          <v-card-title class="subheading font-weight-bold">{{ item.empresa }}
                            <v-spacer/>
                           <a @click="api_downloadCSV($route.params.id_job , item.empresa )"> Baixar CSV </a>
                          </v-card-title>
                          
                          <v-divider></v-divider>

                          <v-list dense>
                            <v-list-item
                              v-for="(key, index) in filteredKeys"
                              :key="index"
                            >
                              <v-list-item-content :class="{ 'blue--text': sortBy === key }">{{ key }}:</v-list-item-content>
                              <v-list-item-content class="align-end" :class="{ 'blue--text': sortBy === key }">
                                <v-chip v-if="key.toLowerCase() === 'encontrados'" color="green" @click="showLogDetail('S',$route.params.id_job, item.empresa, '')">
                                <v-row>
                                   <v-col ><v-icon small>mdi-plus-box-outline</v-icon></v-col>
                                   <v-col >{{ item[key.toLowerCase()] }} </v-col>
                                </v-row> 
                                </v-chip> 
                                <v-chip v-else-if="key.toLowerCase() === 'faltantes' && item[key.toLowerCase()] > 0.0"  color="red" @click="showLogDetail('E',$route.params.id_job, item.empresa, '')"> 
                                <v-row>
                                   <v-col ><v-icon small>mdi-plus-box-outline</v-icon></v-col>
                                   <v-col >{{ item[key.toLowerCase()] }} </v-col>
                                </v-row>    
                                </v-chip> 

                                <v-chip v-else-if="key.toLowerCase() === 'total'" @click="showLogDetail('S',$route.params.id_job, item.empresa, '')" >  
                                   <v-col> <v-icon small>mdi-plus-box-outline</v-icon></v-col>
                                   <v-col>{{ item[key.toLowerCase()] }}</v-col>
                                </v-chip>
                                <!--v-chip v-else="key.toLowerCase() === 'porcentagem'"> {{ item[key.toLowerCase()] }} </v-chip-->
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                        </v-card>
                      </v-col>
                    </v-row>
                  </template>

                  <template v-slot:footer>
                    <v-row class="mt-2" align="center" justify="center">
                      <span class="grey--text">Items per page</span>
                      <v-menu offset-y>
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn
                            dark
                            text
                            color="primary"
                            class="ml-2"
                            v-bind="attrs"
                            v-on="on"
                          >
                            {{ itemsPerPage }}
                            <v-icon>mdi-chevron-down</v-icon>
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            v-for="(number, index) in itemsPerPageArray"
                            :key="index"
                            @click="updateItemsPerPage(number)"
                          >
                            <v-list-item-title>{{ number }}</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>

                      <v-spacer></v-spacer>

                      <span
                        class="mr-4
                        grey--text"
                      >
                        Page {{ page }} of {{ numberOfPages }}
                      </span>
                      <v-btn
                        fab
                        dark
                        color="blue darken-3"
                        class="mr-1"
                        @click="formerPage"
                      >
                        <v-icon>mdi-chevron-left</v-icon>
                      </v-btn>
                      <v-btn
                        fab
                        dark
                        color="blue darken-3"
                        class="ml-1"
                        @click="nextPage"
                      >
                        <v-icon>mdi-chevron-right</v-icon>
                      </v-btn>
                    </v-row>
                  </template>
                </v-data-iterator>

                <v-row justify="center">
                <v-col class="font-weight-medium">
                  É possível cadastrar emails para receber informações quando o processo de download for finalizado. <br/> Se desejar, informe os emails que devem receber a mensagem abaixo:
                   <v-text-field 
                  v-model="v_emails"                  
                  label="Emails (separados por ; )"
                  required
                  outlined></v-text-field>
                </v-col>
                <v-col v-if="showDownloadStatus == '' " style="position: absolute; bottom: 0; text-align: center;">       
                        <!--div style="width:100%; display:flex; padding:30px; justify-content:center;"-->
                            <v-btn v-if="(showDownloadProcessing == false) && (totalGeral > 0)" x-large color="primary" @click="dialogConfirmStart = true" width="80%">Schedular Download</v-btn>
                            <v-btn v-else large color="primary" disabled width="80%">Schedular Download</v-btn>
                </v-col>
                </v-row>
                
                <v-row justify="center">            

                                    <v-col cols="6" v-if="showDownloadProcessing == true">
                                      <v-progress-linear
                                        color="blue darken-4"
                                        indeterminate
                                        rounded
                                        height="10"
                                      ></v-progress-linear>
                                    </v-col>
                </v-row>
                <v-row justify="center">                
                                    <v-col cols="6">
                     
                                        <v-alert v-if="showDownloadStatus == `F`"
                                        type="success" >
                                          Extração Finalizada.
                                          <router-link :to="{path:`/download/${this.$route.params.id_job}`}" width="80%">Clique Aqui</router-link> para ver o resultado.
                                          <!--router-link :to="{path:`/details/${job_return.jobname}`, name:'details', params: {id_job: job_return.jobname} }"-->
                                        </v-alert>

                                        <v-alert v-else-if="showDownloadStatus == `S`"      
                                        dense
                                        type="info" >
                                          Serviço schedulado com sucesso. Em análise sob o job de verificação para "{{c_showName}}"". 
                                          Você pode pode aguardar o retorno do serviço, nessa tela, ou sair, que enviaremos um email quando a extração estiver sido finalizada.
                                        </v-alert>

                                        <v-alert v-else-if="showDownloadStatus == `E`"
                                          dense
                                          outlined
                                          type="error" >
                                          Um erro ocorreu ao schedular o seu serviço. Tente novamente, se o problema persistir, abra um chamado. Não foi possível fazer extração para <b>{{c_showName}}. Erro {{error_desc}}.</b>
                                          <router-link :to="{path:`/`}">Clique Aqui</router-link> e verifique o status de sua execução.
                                        </v-alert>
                     

                                    </v-col>
          
                </v-row>
                        <!--/div-->
              </v-container>

      </v-card-text>

<!-- FINAL MACRO DETAILS -->

   <v-divider></v-divider>

    </v-card>
  </v-card>

<modal_logViewer 
  :props_showLogDetails="props_showLogDetails"
  :props_messageType="props_messageType"
  :props_servico="props_servico"
  :props_jobname="props_jobname"
  :props_company="props_company"  
  :props_code="props_code"
  @closemodal="props_showLogDetails = false"
>Logs de Execução
</modal_logViewer>

<v-dialog v-model="dialogConfirmStart" max-width="500px">
          <v-card>
            <v-card-title class="headline">Você está prestes a iniciar o processo de extração de XMLs com base nos critérios apresentados. Deseja continuar?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="red"  large @click="dialogConfirmStart = false">Cancelar</v-btn>
              <v-spacer></v-spacer>
              <v-btn color="primary" large  @click="api_startDownload()">Iniciar</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>


</div>


</template>

<script>
import DialogLogViewer from "../components/DialogLogViewer";


export default {

components: {
    modal_logViewer: DialogLogViewer
  },


props: {
      //id_job: String
    },

  name: 'app',
data: (vm) => ({

    props_showLogDetails: false,
    props_messageType: "",
    props_servico: "",
    props_jobname: "",
    props_company: "",
    props_code: "",

    opt_empresa: [],
    opt_value_empresa: [],
    showDownloadStatus: "",
    showDownloadProcessing: false,
    totalGeral: 0,
    totalGeralErros: 0,
    v_loadFullPage: true,

    schedulerDownloadStatus: {},

    jobFinishRun: false,

    dialogConfirmStart: false,
    v_emails: "",
    error_desc: "",

    headers: [
          {
            text: 'Critérios de Filtro utilizados',
            align: 'start',
            value: 'name',
            groupable: true,
          },
          { text: 'ID',  value: 'categoria', align: 'right' },
          { text: 'NFE', value: 'dairy', align: 'right' },
        ],
        values: [],

        navJobHeadersToJob : {
            "__metadata": {
                "id": "",
                "uri": "",
                "type": ""
            },
            "jobname": "",
            "date_created": "",
            "time_created": "",
            "user_created": "",
            "status": "",
            "title": "",
            "description": "",
            "date_last_modif": "",
            "time_last_modif": "",
            "user_last_modif": "",
            "doc_type": ""
        },
      
    
    itemsPerPageArray: [3, 6, 12],
    search: "",
    filter: {},
    sortDesc: false,
    page: 1,
    itemsPerPage: 6,
    sortBy: "Empresa",
    keys: ["Empresa", "Total", "Faltantes"],
    items: [],

    companyCheck: []

    
    }),

  methods: {

formatDate (date, spacer) {
        if (!date) return null

        const year = date.substr(0,4)
        const month = date.substr(4,2)
        const day = date.substr(6,2) 
        
        if (!spacer)
          return `${day}/${month}/${year}`
        else  
          return `${year} ${month} ${day}` 
  },

  formatTime (time, spacer) {
        if (!time) return null

        const hour = time.substr(0,2)
        const minutes = time.substr(2,2)
        const seconds = time.substr(4,2) 
        
        if (!spacer)
          return `${hour}:${minutes}:${seconds}`
        else  
          return `${hour} ${minutes} ${seconds}` 
  },



    fetchEmpresas: function () {
          
      const baseURI = this.$host + 'companies?$format=json'
      this.$http.get(baseURI, 
       {
              //auth: {
              //  username: 'tjuliocst',
              //  password: 'elitebox' // Bad password
              //}
       }
  )
      .then((result) => {
         var empresas = [];
          result.data.d.results.forEach((empresa) => {
            empresas.push(empresa.bukrs);
            this.opt_value_empresa.push(empresa.bukrs);
          });

          this.$store.dispatch("nfe_setEmpresas", empresas);
      })
    },

    api_downloadCSV(jobname, empresa_id, option)
    {
      var baseURI

      if (option == "error")
      {
          baseURI = this.$host + `logs(jobname='${jobname}',company='${empresa_id}',code='*')/$value`                                        
      }
      else
      {
          baseURI = this.$host + `jobItems(jobname='${jobname}',company='${empresa_id}')/$value`
      }
      

      this.startDownloadWindow(baseURI)

    },

    startDownloadWindow(urlFile)
    {
          window.open(urlFile);
    },

    api_buscaDetalhesJobScheduler(job_id)
    {
            const baseURI = this.$host + `/jobHeaders(jobname='${job_id}',company='*')?$expand=navJobHeaderToJobHeaders,navJobHeadersToJobFilters,navJobHeadersToJob&$format=json`;

            var navJobHeaderToJobHeaders =
                            {
                                    "results": [
                                        {
                                            "__metadata": {
                                                "id": "",
                                                "uri": "",
                                                "type": ""
                                            },
                                            "jobname": "",
                                            "company": "",
                                            "total": "",
                                            "docsFound": "",
                                            "notFound": "",
                                            "status": "",
                                            "msg": "",
                                            "navJobHeaderToJobLogs": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeadersToJobFilters": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeaderToJobHeaders": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeadersToJobItems": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            },
                                            "navJobHeaderToFiles": {
                                                "__deferred": {
                                                    "uri": ""
                                                }
                                            }
                                        }
                                    ]
                                }
            
            var navJobHeadersToJobFilters = 
                              {
                                "results": [
                                    {
                                        "__metadata": {
                                            "id": "",
                                            "uri": "",
                                            "type": ""
                                        },
                                        "jobname": "",
                                        "fieldname": "",
                                        "de": "",
                                        "ate": ""
                                    } ]
                              }

           
            this.$http
              .get(baseURI, 
               {
                  //auth: {
                  // username: "tjuliocst",
                  // password: "elitebox", // Bad password
                  //},
               }
              ).then((result) => {
                //console.log("resultJobResponse:")

                navJobHeadersToJobFilters = result.data.d.navJobHeadersToJobFilters
                //console.log("Filters>")
                //console.log(navJobHeadersToJobFilters)

                navJobHeadersToJobFilters.results.forEach((filtro) => {
                  var l_name = ((filtro.ate != "")?' até ':'') + filtro.ate
                  
                  //if (filtro.fieldname == 'empresas') 
                  //{
                    var l_tmp = filtro.de.trim()
                    var l_check = this.companyCheck.indexOf(l_tmp)
                  //}
                  
                  if ((filtro.de.length > 0) && ( l_check == -1))
                  {
                    this.values.push({name:`${filtro.de}${l_name}`, categoria: filtro.fieldname, dairy: ''})
                   
                   if (filtro.fieldname == 'empresas')
                    {
                      this.companyCheck.push(filtro.de)
                    }
                  }
                  else
                  {
                     //this.values.push({name:filtro.fieldname +  `-${filtro.de}${l_name}`, category: "error", dairy: ''})
                     //Não exibe data em branco, suprimido
                  } 
                
                })
                
                //console.log("DataResponse - values>")
                //console.log(this.companyCheck)

                navJobHeaderToJobHeaders  = result.data.d.navJobHeaderToJobHeaders
                //onsole.log("DataResponse>")
                //console.log(navJobHeaderToJobHeaders)
                
                navJobHeaderToJobHeaders.results.forEach((resumo) => {

                  this.items.push({empresa:resumo.company, total: resumo.total , encontrados: resumo.docsFound, faltantes: resumo.notFound})
                  this.totalGeral = parseInt(this.totalGeral) + parseInt(resumo.docsFound)
                  this.totalGeralErros = parseInt(resumo.notFound) + parseInt(this.totalGeralErros)

                })

                this.navJobHeadersToJob = result.data.d.navJobHeadersToJob
				
                this.v_loadFullPage = false

                return true

              })
              .catch((error) => {
                //console.log("errro carregar empresas:" + error);
                return false
              });
    },


      returnCompanyCheck(empresa)
      {   
        this.companyCheck.forEach((company) => {
            if(empresa === company)
            return true                       
        })
        
        return false

      },

      nextPage() {
            if (this.page + 1 <= this.numberOfPages) this.page += 1;
          },

          formerPage() {
            if (this.page - 1 >= 1) this.page -= 1;
          },

          updateItemsPerPage(number) {
            this.itemsPerPage = number;
          },

      api_startDownload() {
          const baseURI = this.$host + "downloadDocuments";
          const root_json = { "jobname": this.$route.params.id_job, "email" : this.v_emails }
          this.showDownloadProcessing = true
          this.dialogConfirmStart = false
         
        this.$http
        .post(baseURI, root_json, 
        {
         //withCredentials: true,

           //auth: {
           //  username: "tjuliocst",
           //  password: "elitebox", // Bad password
           // },
            headers: {
                        'x-csrf-token': this.$store.getters.nfe_getCsrfToken,
                        'Content-Type': 'application/json'
                      }

        }
        )
        .then(result => {
            //console.log("envio gateway>")
            //console.log()
            
             //const parser = new DOMParser();
             //const xmlDoc = parser.parseFromString(result.data, "text/xml");
            
            //console.log("Leitura Resutado scheduler job>")
            // this.schedulerDownloadStatus.push({"jobname": (xmlDoc.getElementsByTagName("d:jobname")[0].childNodes[0] != undefined)?xmlDoc.getElementsByTagName("d:jobname")[0].childNodes[0].nodeValue:"", 
            //                                    "company": (xmlDoc.getElementsByTagName("d:company")[0].childNodes[0] != undefined)?xmlDoc.getElementsByTagName("d:company")[0].childNodes[0].nodeValue:"", 
            //                                     "status": (xmlDoc.getElementsByTagName("d:status")[0].childNodes[0] != undefined)?xmlDoc.getElementsByTagName("d:status")[0].childNodes[0].nodeValue:"", 
            //                                        "msg": (xmlDoc.getElementsByTagName("d:msg")[0].childNodes[0] != undefined)?xmlDoc.getElementsByTagName("d:msg")[0].childNodes[0].nodeValue:""})

            this.schedulerDownloadStatus= {"jobname": result.data.d.jobname, 
                                               "company": result.data.d.company, 
                                                "status": result.data.d.status, 
                                                   "msg": result.data.d.msg}

            

            //console.log(" Dados SCHEDULER>")
            //console.log(this.schedulerDownloadStatus)

            this.buscaRetornoDownloadSolicitacao(result.data.d.status, this.$route.params.id_job )          
          
        })
        .catch((error) => {
          //console.log("consultar status job scheduler analise:");
           //console.log(error);
          
        });

      },

       buscaRetornoDownloadSolicitacao(status, id)
       {
                    
          //console.log("scheduler param>")
          //console.log(this.schedulerDownloadStatus)

          if ( this.schedulerDownloadStatus.status == 'S')
            {   var controlInterval
                const retryCount = 5
                const retrySeconds = 5000
                var totalRetry = 43200
                var countRetry = 0
                var check

                this.showDownloadStatus = "S" // scheduled

               controlInterval = setInterval(() => {
                  
                  if (totalRetry <= countRetry)
                  {
                        clearInterval(controlInterval)
                  }                    
                  else
                  {
                    this.api_buscaStatusJobScheduler(id)

                    if (this.jobFinishRun == true)
                    {
                      this.showDownloadStatus = "F" //finished
                      this.showDownloadProcessing = false
                      clearInterval(controlInterval)
                    }

                  }  
                    countRetry++
                    //console.log("aguardando 5 segundos...")
                }, 10000);

              }
            else
            {
              //console.log("erro ao schedular serviço download:")
              ////console.log()
              this.showDownloadStatus = "E" //error
              this.showDownloadProcessing = false
            }

    },

     api_buscaStatusJobScheduler(job_id)
    {       //#! checar a questao de fazer mesma busca mais de uma vez
            const baseURI = this.$host + `/jobs('${job_id}')?$format=json`;

            this.$http
              .get(baseURI, {
                 //auth: {
                 //  username: "tjuliocst",
                 // password: "elitebox", // Bad password
                 //},
              }
              )
              .then((result) => {

                //console.log("resultJobResponse:")
                //console.log(result.data.d.status)
                
                switch (result.data.d.status) {
                  case "C": // download schedulado
                    this.showDownloadStatus = "S"
                    break;
                  case "D": //download finalizado
                    this.jobFinishRun = true 
                    this.showDownloadStatus = "F" 
                    break;
                  case "E": //download finalizado
                    this.jobFinishRun = true 
                    this.showDownloadStatus = "E" 
                    this.error_desc = result.data.d.error_desc
                    break;
                  default:
                    // nothing happened
                    break;
                }
                  

              })
              .catch((error) => {
                //console.log("errro carregar buscaStatusJobScheduler:" + error);
                 this.jobFinishRun = false
              });
    },

    showLogDetail(messageType,jobname, company, code)
    {
          this.props_messageType = messageType,
          this.props_jobname = jobname,
          this.props_company = company,
          this.props_code = "*",
          this.props_showLogDetails = true
    },

  },

  computed: {
    
    c_showName() {
          if (this.navJobHeadersToJob.title == "")
          {return 'N/A';}
          else
          {return this.navJobHeadersToJob.title;}
    },

    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    
    filteredKeys() {
      return this.keys.filter((key) => key !== `Empresa`);
    },
    
    get_id_job()
    {
      return this.$route.params.id_job;
    },

    getShowDownloadStatus()
    {
      return this.showDownloadStatus
    }

    },

   
    beforeMount() {

    if(typeof(this.$store.getters.nfe_getFiltrosEmpresa) == 'undefined' || this.$store.getters.nfe_getFiltrosEmpresa.length == 0)
    {
          this.fetchEmpresas()          
    }

    this.api_buscaDetalhesJobScheduler(this.$route.params.id_job)

    

  }


}
</script>

