    buscaRetornoSchedulerSolicitacao : function(status, id)
    {
                
          if ( status == 'S')
            {   var controlInterval
                const retryCount = 5
                const retrySeconds = 5000
                var totalRetry = 43200
                var countRetry = 0
                var check

                setTimeout(() => {
                }, retrySeconds);
                
                controlInterval = setInterval(() => {
                  //console.log("aguardando 5 segundos...")
                  if (totalRetry <= countRetry)
                  {
                        clearInterval(controlInterval)
                  }                    
                  else
                  {
                    this.api_buscaStatusJobScheduler(id)

                    if (this.jobFinishRun == "true")
                    {
                      this.jobSchedulerFinished = true
                      clearInterval(controlInterval)
                    }

                  }  
                    countRetry++

                }, 10000);



              }

    },

    api_buscaStatusJobScheduler(job_id)
    {       //#! checar a questao de fazer mesma busca mais de uma vez
            const baseURI = this.$host + `/jobs('${job_id}')?$format=json`;

            this.$http
              .get(baseURI, {
                  //auth: {
                  //  username: "tjuliocst",
                  //  password: "elitebox", // Bad password
                  //},
              }
              )
              .then((result) => {


                //console.log("resultJobResponse:")
                //console.log(result.data.d.status)

                if (result.data.d.status == "B")
                  this.jobFinishRun = "true"

              })
              .catch((error) => {
                //console.log("errro carregar buscaStatusJobScheduler:" + error);
                 this.jobFinishRun = "False"
              });
    },

    api_buscaDetalhesJobScheduler(job_id)
    {
            const baseURI = this.$host + `/jobHeaders(jobname='${job_id}',company='*')?$expand=navJobHeaderToJobHeaders,navJobHeadersToJobFilters&$format=json`;

            this.$http
              .get(baseURI, {
                //  auth: {
                //    username: "tjuliocst",
                //    password: "elitebox", // Bad password
                //  },
              })
              .then((result) => {
                //console.log("resultJobResponse:")
                //console.log(result)
                return true
              })
              .catch((error) => {
                //console.log("errro carregar empresas:" + error);
                return false
              });
    },
    
