import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import Login from '../views/Login.vue'
import NfeSaida from '../views/NfeSaida.vue'
import NfeEntrada from '../views/NfeEntrada.vue'
import CteEntrada from '../views/CteEntrada.vue'
import SummaryDetail from '../views/SummaryDetail.vue'
import DownloadPage from '../views/DownloadPage.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Main',
    component: Main
  },
  {
    path: '/nfesaida',
    name: 'NfeSaida',
    component: NfeSaida
  },
  {
    path: '/details/:id_job',
    name: 'SummaryDetail',
    component: SummaryDetail
  },
  {
    path: '/download/:id_job',
    name: 'DownloadPage',
    component: DownloadPage
  },
  {
    path: '/nfeentrada',
    name: 'NfeEntrada',
    component: NfeEntrada
  },
  {
    path: '/cteentrada',
    name: 'CteEntrada',
    component: CteEntrada
  },
  {
    path: '/ajuda',
    name: 'Ajuda',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/Ajuda.vue')
  }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
